.iconDropdown {
	align-items: center;
	background-color: white;
	border-radius: 4px;
	color: black;
	cursor: pointer;
	display: inline-flex;
	font-size: 14px;
	height: 23px;
	margin: 2px;
	min-height: 23px;
	position: relative;
	white-space: nowrap;
}

.iconDropdown:hover {
	color: var(--main-blue-highlight);
}

.iconDropdown > div:first-child::after {
	align-items: center;
	background-color: #e0e0e0;
	border-bottom: solid 1px gray;
	border-radius: 4px;
	border-right: solid 1px gray;
	bottom: 0px;
	content: '▼';
	display: flex;
	justify-content: center;
	padding: 2px 0 1px 0;
	position: absolute;
	right: 0px;
	top: 0px;
	width: 23px;
}

.iconDropdown:hover > div:first-child::after {
	background-color: white;
	border-bottom: solid 1px silver;
	border-right: solid 1px silver;
}

.iconDropdown > div:first-child {
	align-items: center;
	display: flex;
	overflow: hidden;
	padding: 2px 28px 2px 4px;
}

.iconDropdown img {
	margin-right: 4px;
}

.iconDropdown .dropdownList {
	align-self: flex-start;
	background-color: white;
	border-radius: 4px;
	left: 0;
	overflow: hidden;
	position: absolute;
	right: 0;
	z-index: 1000;
}

.iconDropdown .dropdownList > div {
	align-items: center;
	display: flex;
	height: 30px;
	padding: 2px 4px;
}

.iconDropdown .dropdownList > div:hover {
	background-color: var(--main-blue-highlight);
	color: white;
}

.iconDropdown .dropdownList > div.selected {
	background-color: silver !important;
}

.iconDropdown .modalBack {
	background-color: rgba(0, 0, 0, 0.25);
	bottom: 0;
	cursor: default;
	left: 0;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 100;
}

.iconDropdown.disabled {
	background-color: #e0e0e0;
	color: var(--main-blue-dark);
	cursor: default;
	pointer-events: none;
}

.iconDropdown.disabled > div:first-child::after {
	content: '';
	display: none;
}

.Villain .iconDropdown:hover {
	color: var(--main-red-highlight);
}

.Villain .iconDropdown .dropdownList > div:hover {
	background-color: var(--main-red-highlight);
}

.Villain .iconDropdown.disabled {
	color: var(--main-red-dark);
}

@media screen and (max-width: 680px) {
	.iconDropdown {
		height: 30px;
		min-height: 30px;
	}
}